export default (vm, hooks) => ({
  beforeLoad: vm.createAction((state) => ({
    ...state,
    currencies: {
      ...state.currencies,
      error: null,
      loading: true,
    },
  })),

  afterLoadSuccess: vm.createAction((state, currencies) => ({
    ...state,
    currencies: {
      ...state.currencies,
      data: hooks.processCurrencies(currencies),
      error: null,
      loading: false,
    },
  })),

  afterLoadError: vm.createAction((state, error) => ({
    ...state,
    currencies: {
      ...state.currencies,
      error,
      loading: false,
    },
  })),

});



