import angular from 'vendor-bb-angular';

export default (vm) => ({
  beforeSave: vm.createAction((state) => ({
    ...state,
    draft: {
      ...state.draft,
      response: null,
      error: null,
      loading: true,
    },
  })),

  afterSuccesfulSave: vm.createAction((state, response) => ({
    ...state,
    draft: {
      ...state.draft,
      response,
      loading: false,
    },
  })),

  afterErrorSave: vm.createAction((state, error) => ({
    ...state,
    draft: {
      ...state.draft,
      error,
      loading: false,
    },
  })),

  clearMessages: vm.createAction((state) => ({
    ...state,
    draft: {
      ...state.draft,
      error: null,
      response: null,
    },
  })),

  beforeLoad: vm.createAction((state) => ({
    ...state,
    payment: {
      ...state.payment,
      isDraft: true,
    },
  })),

  clonePayment: vm.createAction((state) => ({
    ...state,
    draft: {
      ...state.draft,
      data: angular.copy(state.payment.data),
    },
  })),

  hasChanged: vm.createSelector((state) => !angular.equals(state.draft.data, state.payment.data)),
});



