export default (vm, hooks) => ({
  setup: vm.createAction((state, privileges) => ({
    ...state,
    privileges: {
      ...state.privileges,
      configuration: hooks.processPrivileges(privileges),
    },
  })),

  beforeRequest: vm.createAction((state) => ({
    ...state,
    privileges: {
      ...state.privileges,
      response: null,
      error: null,
      loading: true,
    },
  })),

  afterSuccesfulRequest: vm.createAction((state, response) => ({
    ...state,
    privileges: {
      ...state.privileges,
      response,
      loading: false,
    },
  })),

  afterErrorRequest: vm.createAction((state, error) => ({
    ...state,
    privileges: {
      ...state.privileges,
      error,
      loading: false,
    },
  })),

});



