import angular from 'vendor-bb-angular';
import {
  getInitialPayment,
  getInitialAmount,
  getInitialCounter,
} from './initial-state';

export default (vm, hooks) => ({
  init: vm.createAction((state, options) => ({
    ...state,
    payment: {
      ...state.payment,
      ...options,
    },
  })),

  reset: vm.createAction((state) => ({
    ...state,
    payment: {
      ...state.payment,
      saveNewContact: false,
      isDraft: false,
      createdAt: null,
      messageToBeneficiaryCounter: getInitialCounter(),
      messageToBankCounter: getInitialCounter(),
      isCorrespondentEnabled: false,
      isIntermediaryEnabled: false,
      hasToAmountChanged: false,
      data: getInitialPayment(hooks),
    },
    review: {
      ...state.review,
      isDiscardPaymentPopupOpen: false,
    },
  })),

  show: vm.createAction((state) => ({
    ...state,
    payment: {
      ...state.payment,
      hidden: false,
    },
  })),

  hide: vm.createAction((state) => ({
    ...state,
    payment: {
      ...state.payment,
      hidden: true,
    },
  })),

  startCreate: vm.createAction((state, debitAccounts) => ({
    ...state,
    debitAccounts,
  })),

  beforeCreate: vm.createAction((state) => ({
    ...state,
    payment: {
      ...state.payment,
      loading: true,
    },
  })),

  afterCreateSuccess: vm.createAction((state, response) => ({
    ...state,
    payment: {
      ...state.payment,
      response,
      error: null,
      loading: false,
    },
  })),

  afterCreateError: vm.createAction((state, error) => ({
    ...state,
    payment: {
      ...state.payment,
      error,
      loading: false,
    },
  })),

  beforeApprove: vm.createAction((state) => ({
    ...state,
  })),

  afterApproveSuccess: vm.createAction((state) => ({
    ...state,
    notifications: [...state.notifications, { type: 'approve', result: true }],
  })),

  afterApproveError: vm.createAction((state) => ({
    ...state,
    notifications: [...state.notifications, { type: 'approve', result: false }],
  })),

  clearMessages: vm.createAction((state) => ({
    ...state,
    notifications: [],
    payment: {
      ...state.payment,
      error: null,
      response: null,
    },
  })),

  saveContact: vm.createSelector((state) => state.payment.saveNewContact),

  setPayment: vm.createAction((state, payment) => {
    const hasToAmountChanged = Boolean(
      payment.data.toAmount
      && payment.data.amount.currency !== hooks.defaultCurrency()
    );
    const getAmount = (amount) => ({
      ...(amount || {}),
    });
    const amount = !hasToAmountChanged ?
      getAmount(payment.data.amount) : getInitialAmount(hooks);
    const toAmount = !hasToAmountChanged ?
      getAmount(payment.data.toAmount) : getAmount(payment.data.amount);

    return {
      ...state,
      payment: {
        ...state.payment,
        createdAt: payment.createdAt,
        isCorrespondentEnabled:
          Boolean(payment.data.beneficiary && payment.data.beneficiary.correspondentBank),
        isIntermediaryEnabled:
          Boolean(payment.data.beneficiary && payment.data.beneficiary.intermediaryBank),
        hasToAmountChanged,
        data: {
          ...state.payment.data,
          id: payment.data.id,
          amount,
          toAmount,
          debitAccount: payment.data.debitAccount ?
            angular.copy(state.debitAccounts.data
              .find((account) =>
                account.id === payment.data.debitAccount.id
              )
            )
            : null,
          paymentReference: payment.data.paymentReference,
          beneficiary: {
            ...state.payment.data.beneficiary,
            ...angular.copy(payment.data.beneficiary),
            postalAddress: {
              ...state.payment.data.beneficiary.postalAddress,
              ...payment.data.beneficiary ? angular.copy(payment.data.beneficiary.postalAddress)
                : null,
            },
            bank: {
              ...state.payment.data.beneficiary.bank,
              ...payment.data.beneficiary ? angular.copy(payment.data.beneficiary.bank) : null,
              postalAddress: {
                ...state.payment.data.beneficiary.bank.postalAddress,
                ...payment.data.beneficiary && payment.data.beneficiary.bank ?
                  angular.copy(payment.data.beneficiary.bank.postalAddress) : null,
              },
            },
            correspondentBank: {
              ...state.payment.data.beneficiary.correspondentBank,
              ...payment.data.beneficiary ? angular.copy(payment.data.beneficiary.correspondentBank)
                : null,
            },
            intermediaryBank: {
              ...state.payment.data.beneficiary.intermediaryBank,
              ...payment.data.beneficiary ? angular.copy(payment.data.beneficiary.intermediaryBank)
                : null,
            },
          },
          schedule: {
            ...state.payment.data.schedule,
            ...angular.copy(payment.data.schedule),
          },
          endingType: payment.data.endingType,
        },
      },
    };
  }),

  hasChanged: vm.createSelector((state) =>
   !angular.equals(getInitialPayment(hooks), state.payment.data)
  ),

  showPaymentSelectorConfirmation: vm.createAction((state, paymentType) => ({
    ...state,
    paymentTypeSelector: {
      confirmOpened: true,
      selectedType: paymentType,
    },
  })),
});



