import { createAccountsLoadErrorMessage, createPaymentErrorMessage } from './message';
import { Resource, Privileges, Event } from './constants';

const isNotEmptyString = (str) => Boolean(typeof str === 'string' && str.trim().length);
const cachedDebitAccounts = {};
const shareSameKeyValues = (source, target) => {
  if (source === null || typeof source !== 'object') {
    return source === target;
  }

  return !Object.keys(source).find((key) => {
    const value = source[key];
    if (typeof value === 'object') {
      return !shareSameKeyValues(value, target[key]);
    }
    return value !== target[key];
  });
};

export default (model, vm, bbPermissions, Promise, bus) => {
  const getDebitAccounts = (searchQuery) => {
    let promise;
    vm.debitAccounts.beforeLoad();
    const parameters = {
      searchQuery: isNotEmptyString(searchQuery) ? searchQuery : null,
      debitAccount: true,
    };

    if (searchQuery in cachedDebitAccounts) {
      promise = Promise.resolve(cachedDebitAccounts[searchQuery]);
    } else {
      promise = model.getContextArrangements(model.Context[vm.state.payment.type], parameters)
        .then((debitAccounts) => {
          cachedDebitAccounts[searchQuery] = debitAccounts;
          return debitAccounts;
        });
    }

    return promise.then((arrangements) => {
      vm.debitAccounts.afterLoadSuccess(arrangements);
      return vm.state.debitAccounts.data;
    })
      .catch((error) => {
        vm.debitAccounts.afterLoadError(createAccountsLoadErrorMessage(error));
        throw error;
      });
  };

  /**
   * @description
   * Get credit accounts available based on params provided
   *
   * @name Service#getBeneficiaryAccounts
   * @type {Function}
   *
   * @param {Object} params Request parameters and callback
   * @returns {Promise.<Object>} A Promise with flat accounts list and total number of items
   */
  const getBeneficiaryAccounts = (params) => {
    vm.creditAccounts.beforeLoad();

    return model.getContextArrangements(model.Context[vm.state.payment.type], params.request, true)
      .then(response => {
        vm.creditAccounts.afterLoadSuccessTotals({
          data: params.overwrite ? response.data :
            [...(vm.state.creditAccounts.data || []), ...response.data],
          totalCount: response.totalCount,
        });

        // modify request params
        const hasMore = response.data.length > 0 &&
          vm.state.creditAccounts.data.length < response.totalCount;

        if (typeof params.done === 'function') {
          params.done(hasMore);
        }
      })
      .catch((err) => {
        if (typeof params.done === 'function') {
          params.done(err);
        }

        vm.creditAccounts.afterLoadError(createAccountsLoadErrorMessage(err));
        throw err;
      });
  };

  /**
   * @description
   * Get contacts available based on params provided
   *
   * @name Service#getBeneficiaryContacts
   * @type {Function}
   *
   * @param {Object} params Request parameters and callback function
   * @returns {Promise.<Object>} A Promise with flat contacts list with total items number
   */
  const getBeneficiaryContacts = (params) => {
    vm.contacts.beforeLoad();

    return model.getContacts(params.request)
      .then(response => {
        vm.contacts.afterLoadSuccessTotals({
          data: params.overwrite ? response.data :
            [...(vm.state.contacts.data || []), ...response.data],
          totalCount: response.totalCount,
        });

        // modify request params
        const hasMore = response.data.length > 0 &&
          vm.state.contacts.data.length < response.totalCount;

        if (typeof params.done === 'function') {
          params.done(hasMore);
        }
      })
      .catch((err) => {
        if (typeof params.done === 'function') {
          params.done(err);
        }

        vm.contacts.afterLoadError(err);
      });
  };

  /**
   * @name Service
   * @type {Object}
   */
  return {
    start: () => {
      vm.create.init({
        hidden: vm.state.preferences.hidden,
      });
      vm.privileges.setup({
        configuration: Privileges,
        constants: {
          resource: Resource,
          function: model.Context,
        },
      });
    },

    getDebitAccounts,

    getCreditAccounts: () => {
      vm.creditAccounts.beforeLoad();
      const parameters = {
        creditAccount: true,
      };
      return model.getContextArrangements(model.Context[vm.state.payment.type], parameters)
        .then((arrangements) => {
          vm.creditAccounts.afterLoadSuccess(arrangements);
          return vm.state.creditAccounts.data;
        })
        .catch((error) => {
          vm.creditAccounts.afterLoadError(createAccountsLoadErrorMessage(error));
          throw error;
        });
    },

    selectDebitAccount: (account) => {
      vm.debitAccounts.select(account);
    },

    getContactsByName: (name) => {
      vm.contacts.beforeLoad();
      return model.getContactsByName(name)
        .then((contacts) => {
          vm.contacts.afterLoadSuccess(contacts);
          return vm.contacts.getContacts();
        })
        .catch(vm.contacts.afterLoadError);
    },

    setBeneficiary: (beneficiary) => {
      if (beneficiary.name) {
        vm.beneficiary.setName(beneficiary.name);
      }
      vm.beneficiary.setPostalAddress(beneficiary.postalAddress);
      vm.beneficiary.setBank(beneficiary.bank);
    },

    resetBeneficiary: () => {
      vm.beneficiary.reset();
    },

    resetPayment: () => {
      vm.create.reset();
    },

    clearMessages: () => {
      vm.create.clearMessages();
      vm.draft.clearMessages();
    },

    createContact: (contact) => model.createContact(contact),

    createPayment: (payment) => {
      vm.create.beforeCreate();
      return model.createPaymentOrder(payment)
        .then((response) => {
          if (vm.state.payment.isDraft) {
            model.deleteDraft(vm.state.payment.data.id);
          }
          vm.create.afterCreateSuccess(response.data.status);
          return response.data;
        })
        .catch((error) => {
          vm.create.afterCreateError(createPaymentErrorMessage(error));
          throw error;
        });
    },

    updatePayment: (payment) =>
      model.updatePayment(payment.id, payment)
        .then((response) => {
          vm.create.afterCreateSuccess(response.data.status);
          return response.data;
        })
        .catch((error) => {
          vm.create.afterCreateError(createPaymentErrorMessage(error));
          throw error;
        }),

    approvePayment: (paymentId) => {
      vm.create.beforeApprove();
      return model.authorizePayment(paymentId)
        .then(vm.create.afterApproveSuccess)
        .catch((error) => {
          vm.create.afterApproveError(createPaymentErrorMessage(error));
          throw error;
        });
    },

    saveDraft: (payment) => {
      vm.draft.beforeSave();
      return model.saveDraft(payment)
        .then(vm.draft.afterSuccesfulSave)
        .then(vm.draft.clonePayment)
        .catch(vm.draft.afterErrorSave);
    },

    updateDraft: (payment) => {
      vm.draft.beforeSave();
      return model.editDraft(payment.id, payment)
        .then(vm.draft.clonePayment)
        .catch(vm.draft.afterErrorSave);
    },

    getRate: (rateParams) => {
      vm.rate.beforeRequest();
      return model.getRate(rateParams)
        .then(vm.rate.afterSuccesfulRequest)
        .catch(vm.rate.afterErrorRequest);
    },

    getCurrencies: () => {
      vm.currencies.beforeLoad();
      return model.getCurrencies()
        .then(vm.currencies.afterLoadSuccess)
        .catch(vm.currencies.afterLoadError);
    },

    /**
     * @name Service#updateAmount
     * @description
     * After to amount has been manually changed, update from amount automatically.
     *
     * @type {Function}
     * @param {Amount} values Calculated from amount based on to amount
     */
    updateAmount: (values) => {
      vm.payment.updateAmount(values);
    },

    /**
     * @name Service#updateToAmount
     * @description
     * After from amount has been manually changed, update to amount automatically.
     *
     * @type {Function}
     * @param {Amount} values Calculated to amount based on from amount
     */
    updateToAmount: (values) => {
      vm.payment.updateToAmount(values);
    },

    /**
     * @name Service#updateToAmountByCurrency
     * @description
     * After to currency has been manually changed, update to amount automatically.
     *
     * @type {Function}
     * @param {Amount} values Calculated to amount based on from amount
     */
    updateToAmountByCurrency: (values) => {
      vm.payment.updateToAmountByCurrency(values);
    },

    /**
     * @name Service#updateFromEditChanged
     * @description
     * Update from edit changed value,to know if any changed after set up edited payment
     *
     * @type {Function}
     * @param {Boolean} value true/false
     */
    updateFromEditChanged: (value) => vm.payment.updateFromEditChanged(value),

    getPermissions: () => {
      vm.privileges.beforeRequest();
      return bbPermissions.getPermissions(vm.state.privileges.configuration)
        .then(vm.privileges.afterSuccesfulRequest, vm.privileges.afterErrorRequest);
    },

    setDraftPayment: (initialPaymentData) => {
      vm.draft.beforeLoad();
      getDebitAccounts('')
        .then(() => {
          vm.create.setPayment(initialPaymentData);
          vm.draft.clonePayment();
        });
    },

    setEditPayment: (paymentData) => {
      vm.create.reset();
      vm.create.clearMessages();
      getDebitAccounts('')
        .then(() => {
          vm.create.setPayment(paymentData);
        });
    },

    deleteDraft: (id) => model.deleteDraft(id),

    show: () => vm.create.show(),

    hide: () => vm.create.hide(),

    editRole: () => vm.state.preferences.editRole,

    get PaymentType() {
      return model.PaymentType;
    },

    paymentHasChanged: () => {
      if (vm.state.payment.isDraft) {
        return vm.draft.hasChanged();
      }

      return !shareSameKeyValues(vm.helpers.initialPayment, vm.state.payment.data);
    },

    showPaymentSelectorConfirmation: (paymentType) => {
      vm.create.showPaymentSelectorConfirmation(paymentType);
    },

    publishPaymentTypeChange: (paymentType) => {
      bus.publish(Event.PAYMENT_TYPE_SELECTED, paymentType);
    },

    publishPaymentEditDone: () => bus.publish(Event.PAYMENT_EDIT_DONE),

    publishPaymentEditCanceled: () => bus.publish(Event.PAYMENT_EDIT_CANCELLED),

    getBeneficiaryAccounts,
    getBeneficiaryContacts,
  };
};

/**
 * @typedef Amount
 * @type {Object}
 * @property {Number} value    - Amount numerical value
 * @property {String} currency - ISO 4217 currency code
 */



