export default (vm, hooks) => ({
  beforeLoad: vm.createAction((state) => ({
    ...state,
    contacts: {
      ...state.contacts,
      error: null,
      loading: true,
    },
  })),

  afterLoadSuccess: vm.createAction((state, contacts) => ({
    ...state,
    contacts: {
      ...state.contacts,
      data: hooks.processContacts(contacts),
      error: null,
      loading: false,
    },
  })),

  afterLoadSuccessTotals: vm.createAction((state, payload) => ({
    ...state,
    contacts: {
      ...state.contacts,
      data: hooks.processContacts(payload.data),
      error: null,
      loading: false,
      totalCount: payload.totalCount,
    },
  })),

  afterLoadError: vm.createAction((state, error) => ({
    ...state,
    contacts: {
      ...state.contacts,
      error,
      loading: false,
    },
  })),

  getContacts: vm.createSelector((state) => state.contacts.data),
});



