import angular from 'vendor-bb-angular';

export default (vm, hooks) => ({
  beforeLoad: vm.createAction((state) => ({
    ...state,
    debitAccounts: {
      ...state.debitAccounts,
      loading: true,
    },
  })),

  afterLoadSuccess: vm.createAction((state, debitAccounts) => ({
    ...state,
    debitAccounts: {
      ...state.debitAccounts,
      data: hooks.processDebitAccounts(debitAccounts),
      error: null,
      loading: false,
    },
  })),

  afterLoadError: vm.createAction((state, error) => ({
    ...state,
    debitAccounts: {
      ...state.debitAccounts,
      error,
      loading: false,
    },
  })),

  select: vm.createAction((state, account) => ({
    ...state,
    payment: {
      ...state.payment,
      data: {
        ...state.payment.data,
        debitAccount: angular.copy(account),
      },
    },
  })),
});



