export default vm => ({
  updateAmount: vm.createAction((state, values) => ({
    ...state,
    payment: {
      ...state.payment,
      hasToAmountChanged: true,
      data: {
        ...state.payment.data,
        amount: {
          ...state.payment.data.amount,
          ...values,
        },
      },
    },
  })),

  updateToAmount: vm.createAction((state, values) => ({
    ...state,
    payment: {
      ...state.payment,
      hasToAmountChanged: false,
      data: {
        ...state.payment.data,
        toAmount: {
          ...state.payment.data.toAmount,
          ...values,
        },
      },
    },
  })),

  updateToAmountByCurrency: vm.createAction((state, values) => ({
    ...state,
    payment: {
      ...state.payment,
      data: {
        ...state.payment.data,
        toAmount: {
          ...state.payment.data.toAmount,
          ...values,
        },
      },
    },
  })),

  updateFromEditChanged: vm.createAction((state, value) => ({
    ...state,
    payment: {
      ...state.payment,
      fromEditChanged: value,
    },
  })),

});



