import angular from 'vendor-bb-angular';

export default (vm, hooks) => ({
  beforeLoad: vm.createAction((state) => ({
    ...state,
    creditAccounts: {
      ...state.creditAccounts,
      loading: true,
    },
  })),

  afterLoadSuccess: vm.createAction((state, creditAccounts) => ({
    ...state,
    creditAccounts: {
      ...state.creditAccounts,
      data: hooks.processCreditAccounts(creditAccounts),
      error: null,
      loading: false,
    },
  })),

  afterLoadSuccessTotals: vm.createAction((state, payload) => ({
    ...state,
    creditAccounts: {
      ...state.creditAccounts,
      data: hooks.processCreditAccounts(payload.data),
      error: null,
      loading: false,
      totalCount: payload.totalCount,
    },
  })),

  afterLoadError: vm.createAction((state, error) => ({
    ...state,
    creditAccounts: {
      ...state.creditAccounts,
      error,
      loading: false,
    },
  })),

  select: vm.createAction((state, account) => ({
    ...state,
    payment: {
      ...state.payment,
      data: {
        ...state.payment.data,
        creditAccount: angular.copy(account),
      },
    },
  })),
});



