/**
 * @module widget-bb-initiate-us-payment-ng
 *
 * @description
 * Initiate US payment widget
 */
import angular from 'vendor-bb-angular';

import extendHooks, { extensionContextKey } from 'lib-bb-widget-extension-ng';

import eventBusModuleKey, { eventBusKey } from 'lib-bb-event-bus-ng';
import widgetModuleKey, { widgetKey } from 'lib-bb-widget-ng';
import bbIntentModuleKey, { bbIntentKey } from 'lib-bb-intent-ng';

import modelPaymentOrdersModuleKey, { modelPaymentOrdersKey } from 'model-bb-payment-orders-ng';
import bbPermissionsModuleKey, { bbPermissionsKey } from 'lib-bb-permissions-ng';

import stateContainerModuleKey, { bbStateContainerKey } from 'lib-bb-state-container-ng';

import bbExtensionIntentsModuleKey, {
  extensionIntentsContextKey,
} from 'lib-bb-extension-intents-ng';

import Service from './service';
import ViewModel from './view-model';

import Router from './router';
import PostActions from './post-actions';
import Controller from './controller';

import * as defaultHooks from './default-hooks';

const moduleKey = 'widget-bb-initiate-us-payment-ng';
const hooksKey = `${moduleKey}:hooks`;
const serviceKey = `${moduleKey}:service`;
const viewModelKey = `${moduleKey}:view-model`;
const routerKey = `${moduleKey}:router`;
const postActionsKey = `${moduleKey}:post-actions`;


export const Preference = {
  URGENT: 'bb.payment.urgent',
  RECURRING: 'bb.payment.recurring',
  DISMISS_TIME: 'bb.payment.notificationDismissTime',
  TYPE_SELECTOR_ENABLED: 'bb.payment.typeSelector.enabled',
  HIDDEN: 'bb.payment.hidden',
  PAGE_SIZE: 'bb.payment.list.pageSize',
  BENEFICIARY_SELECT_TYPE: 'bb.payment.beneficiary.select.type',
  EDIT_ROLE: 'bb.payment.edit.role',
};

const getPreferences = (bbWidget) => {
  const preferences = {};

  preferences.urgent = bbWidget.getBooleanPreference(Preference.URGENT);
  preferences.recurring = bbWidget.getBooleanPreference(Preference.RECURRING);
  preferences.editRole = bbWidget.getBooleanPreference(Preference.EDIT_ROLE);
  preferences.dismissTime = bbWidget.getLongPreference(Preference.DISMISS_TIME);
  preferences.typeSelectorEnabled = bbWidget.getBooleanPreference(Preference.TYPE_SELECTOR_ENABLED);
  preferences.hidden = bbWidget.getBooleanPreference(Preference.HIDDEN);
  preferences.pageSize = bbWidget.getLongPreference(Preference.PAGE_SIZE);
  preferences.beneficiarySelectType =
    bbWidget.getStringPreference(Preference.BENEFICIARY_SELECT_TYPE);

  return preferences;
};

export default angular
  .module(moduleKey, [
    eventBusModuleKey,
    modelPaymentOrdersModuleKey,
    stateContainerModuleKey,
    widgetModuleKey,
    bbIntentModuleKey,
    bbPermissionsModuleKey,
    bbExtensionIntentsModuleKey,
  ])

  .factory(hooksKey, extendHooks(defaultHooks))

  .factory(viewModelKey, [
    bbStateContainerKey,
    hooksKey,
    /* into */
    ViewModel,
  ])

  .factory(serviceKey, [
    modelPaymentOrdersKey,
    viewModelKey,
    bbPermissionsKey,
    '$q',
    eventBusKey,
    /* into */
    Service,
  ])

  .factory(routerKey, [
    bbStateContainerKey,
    /* into */
    Router,
  ])

  .factory(postActionsKey, [
    serviceKey,
    routerKey,
    /* into */
    PostActions,
  ])

  .controller('USPaymentController', [
    // dependencies to inject
    serviceKey,
    routerKey,
    eventBusKey,
    postActionsKey,
    /* into */
    Controller,
  ])

  .factory(extensionContextKey, [
    serviceKey,
    routerKey,
    postActionsKey,
    '$timeout',
    /* into */
    (service, router, postActions, $timeout) => ({
      service,
      router,
      postActions,
      $timeout,
    }),
  ])

  .factory(extensionIntentsContextKey, [
    serviceKey,
    '$timeout',
    /* into */
    (service, $timeout) => ({
      service,
      $timeout,
    }),
  ])

  .run([
    bbIntentKey, eventBusKey, widgetKey, bbStateContainerKey, viewModelKey,
    (bbIntent, bus, widget, bbStateContainer, viewModel) => {
      viewModel.init(getPreferences(widget));

      bbIntent.init().then(() => {
        bus.publish('cxp.item.loaded', {
          id: widget.getId(),
        });
      });
    },
  ])

  .name;



