import angular from 'vendor-bb-angular';
import { singleTransfer, EndingType } from '../constants';

const getInitialPostalAddress = (hooks) => ({
  addressLine1: null,
  addressLine2: null,
  streetName: null,
  postCode: null,
  town: null,
  countrySubDivision: null,
  country: hooks.defaultCountry(),
});

const getInitialSchedule = (hooks, today = new Date()) => ({
  startDate: new Date(
    today.getFullYear(),
    today.getMonth(),
    today.getDate(),
    0, 0, 0
  ),
  endDate: new Date(
    today.getFullYear(),
    today.getMonth() + 1,
    today.getDate(),
    0, 0, 0
  ),
  transferFrequency: angular.copy(singleTransfer),
  repeat: hooks.defaultScheduleRepeat(),
});

export const getInitialBeneficiary = (hooks) => ({
  name: null,
  postalAddress: getInitialPostalAddress(hooks),
  message: null,
  bank: {
    name: null,
    accountNumber: null,
    accountType: null,
    code: null,
    bic: null,
    postalAddress: getInitialPostalAddress(hooks),
    message: null,
  },
  correspondentBank: {
    name: null,
    code: null,
  },
  intermediaryBank: {
    type: 'SWIFT/BIC',
    name: null,
    code: null,
    bic: null,
    postalAddress: getInitialPostalAddress(hooks),
  },
});

export const getInitialAmount = (hooks) => ({
  value: null,
  currency: hooks.defaultCurrency() || undefined,
});

export const getInitialToAmount = () => ({
  value: null,
  currency: undefined,
});

export const getInitialPayment = (hooks) => ({
  id: null,
  amount: getInitialAmount(hooks),
  toAmount: getInitialToAmount(),
  debitAccount: null,
  paymentReference: null,
  beneficiary: getInitialBeneficiary(hooks),
  schedule: getInitialSchedule(hooks),
  endingType: EndingType.NEVER,
  urgent: false,
  exchangeRate: 1,
});

export const getInitialCounter = () => ({
  counter: 0,
  isValid: true,
});

export default (hooks) => ({
  payment: {
    data: getInitialPayment(hooks),
    type: hooks.defaultPaymentType(),
    saveNewContact: false,
    isDraft: false,
    createdAt: null,
    messageToBeneficiaryCounter: getInitialCounter(),
    messageToBankCounter: getInitialCounter(),
    isCorrespondentEnabled: false,
    isIntermediaryEnabled: false,
    error: null,
    response: null,
    loading: false,
    hidden: true,
    hasToAmountChanged: false,
    fromEditChanged: false,
  },
  review: {
    isDiscardPaymentPopupOpen: false,
  },
  form: {
    isCancelPaymentPopupOpen: false,
  },
  debitAccounts: {
    data: null,
    error: null,
    loading: false,
  },
  creditAccounts: {
    data: null,
    error: null,
    loading: false,
  },
  currencies: {
    data: null,
    error: null,
    loading: false,
  },
  draft: {
    deleteConfirmOpened: false,
    editConfirmOpened: false,
    data: null,
    response: null,
    error: null,
    loading: false,
  },
  paymentTypeSelector: {
    confirmOpened: false,
    selectedType: null,
  },
  privileges: {},
  preferences: {},
  contacts: {
    data: null,
    error: null,
    loading: false,
  },
  rate: {
    data: null,
    error: null,
    loading: false,
  },
  notifications: [],
  template: '#widget-bb-initiate-us-payment-ng/form.html',
  route: {
    name: 'form',
    params: {},
  },
});



