import angular from 'vendor-bb-angular';
import { getInitialBeneficiary } from './initial-state';

export default (vm, hooks) => ({
  reset: vm.createAction((state) => ({
    ...state,
    payment: {
      ...state.payment,
      saveNewContact: false,
      data: {
        ...state.payment.data,
        beneficiary: getInitialBeneficiary(hooks),
      },
    },
  })),

  setName: vm.createAction((state, name) => ({
    ...state,
    payment: {
      ...state.payment,
      data: {
        ...state.payment.data,
        beneficiary: {
          ...state.payment.data.beneficiary,
          name,
        },
      },
    },
  })),

  setPostalAddress: vm.createAction((state, postalAddress) => ({
    ...state,
    payment: {
      ...state.payment,
      data: {
        ...state.payment.data,
        beneficiary: {
          ...state.payment.data.beneficiary,
          postalAddress: {
            ...state.payment.data.beneficiary.postalAddress,
            ...angular.copy(postalAddress),
          },
        },
      },
    },
  })),

  setBank: vm.createAction((state, bank) => ({
    ...state,
    payment: {
      ...state.payment,
      data: {
        ...state.payment.data,
        beneficiary: {
          ...state.payment.data.beneficiary,
          bank: {
            ...state.payment.data.beneficiary.bank,
            ...angular.copy(bank),
            postalAddress: {
              ...state.payment.data.beneficiary.bank.postalAddress,
              ...angular.copy(bank.postalAddress),
            },
          },
        },
      },
    },
  })),

});



