import {
  default as createInitialState,
  getInitialPayment,
} from './initial-state';
import create from './create';
import debitAccounts from './debit-accounts';
import creditAccounts from './credit-accounts';
import contacts from './contacts';
import beneficiary from './beneficiary';
import draft from './draft';
import privileges from './privileges';
import rate from './rate';
import currencies from './currencies';
import payment from './payment';

export default (vm, hooks) => ({
  init: vm.createAction((state = createInitialState(hooks), preferences = {}) => ({
    ...state,
    preferences,
  })),

  create: create(vm, hooks),

  debitAccounts: debitAccounts(vm, hooks),

  creditAccounts: creditAccounts(vm, hooks),

  contacts: contacts(vm, hooks),

  beneficiary: beneficiary(vm, hooks),

  draft: draft(vm, hooks),

  privileges: privileges(vm, hooks),

  rate: rate(vm, hooks),

  currencies: currencies(vm, hooks),

  payment: payment(vm, hooks),

  get state() { return vm.getState(); },

  helpers: {
    initialPayment: getInitialPayment(hooks),
  },
});



