export default (vm, hooks) => ({
  setup: vm.createAction((state, rate) => ({
    ...state,
    rate: hooks.processRate(rate),
  })),

  beforeRequest: vm.createAction((state) => ({
    ...state,
    rate: {
      ...state.rate,
      data: null,
      error: null,
      loading: true,
    },
  })),

  afterSuccesfulRequest: vm.createAction((state, data) => ({
    ...state,
    rate: {
      ...state.rate,
      data,
      loading: false,
    },
  })),

  afterErrorRequest: vm.createAction((state, error) => ({
    ...state,
    rate: {
      ...state.rate,
      error,
      loading: false,
    },
  })),

});



